const error = {
    // 
    UnknownError: '',
    // token
    TokenNotValidError: 'Token',
    TokenNotProvidedError: 'Token',
    TokenExpiredError: 'Token',
    // 
    UserNotFoundError: '',
    PasswordIncorrectError: '',
    // 
    UsernameDuplicateError: ''
};

var _code = 0;
for (let name in error) {
    error[name] = {
        code: _code++,
        name: name,
        msg: error[name]
    }
}

module.exports = error;